* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Karla", sans-serif;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  width: 100%;
  padding: 0 3rem;
}

/* header section commons */
.header__content,
.section__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  color: #000;
}

.header__content h1,
.my-info h1,
.section__content h1 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.header__content p,
.my-info p,
.section__content p {
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 2.2rem;
  margin-bottom: 1.5rem;
}

.header__content li,
.section__content li {
  display: inline-block;
}

.header__content li:not(:first-child),
.section__content li:not(:first-child) {
  margin-left: 1rem;
}

/* header */
.header__content {
  height: 100vh;
}

.my-info {
  margin-bottom: 50vh;
}

.header__content a {
  color: royalblue;
  font-size: 1.15rem;
  transition: color 0.3s ease;
}

.header__content a:hover {
  color: darkblue;
}

.header__content ul .last-list {
  font-size: 1.15rem;
  cursor: pointer;
}

/* SECTIONS */
.section__content {
  margin-bottom: 10rem;
}

.section__content a {
  border: 2px solid #000;
  padding: 0.5rem 1rem;
  color: black;
  display: block;
  font-size: 1.15rem;
  transition: all 0.3s ease;
}
.section__content a:hover {
  background: #000;
  color: #fff;
}

.section__content img {
  margin-top: 4rem;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.137), 0 10px 30px rgba(0, 0, 0, 0.07);
  border-radius: 4px;
  width: 100%;
}

/* FOOTER */
footer {
  background-color: #fafafa;
  position: relative;
}

.footer__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem 0;
}

.footer__content p {
  color: #aeafb0;
}

/* MEDIA QUERIES */
@media (min-width: 768px) {
  .container {
    width: 768px;
    margin: 0 auto;
  }

  .header__content h1,
  .section__content h1,
  .my-info h1 {
    font-size: 3.6rem;
    font-weight: 700;
    margin-bottom: 1.5rem;
  }

  .header__content p,
  .section__content p,
  .my-info p {
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 2.3rem;
    margin-bottom: 1.5rem;
  }

  .section__content img {
    width: 45%;
  }
  .image-wrapper {
    display: flex;
    justify-content: space-between;
  }
}
